import React,{useEffect,useState,useRef} from 'react';
import {Container ,Card,Row, Col, Button} from 'react-bootstrap';
import {useDispatch, useSelector} from 'react-redux'; 
import { useFormik } from 'formik';
import * as yup from 'yup';
import student1 from '../../student1.png'
import admin from '../../admin.png'
import { getallorders,selection,getselectedorders,getroutes,setroute} from '../../store/driverSlice';
function Dashboard(){
    const loggeduser=useSelector(state=>state.authentic.loggeduser);
    const u=JSON.parse(localStorage.getItem('user')) 
    var [s,sets]=React.useState(0)
    const [pd,setpd]=useState(0)
    const [rt,setrt]=useState(0)
    const dispatch=useDispatch();
    const allorders=useSelector((state)=>state.driver.allorders[0])
    const selectedorders=useSelector((state)=>state.driver.selectedorders[0])
    const routes=useSelector((state)=>state.driver.routes[0])

    function handleallorders(){
        sets(0)
    }
    function handleselectedorders(){
        sets(1)
    }
    function handleroutedis(){
        if(rt===0){
            setrt(1)
        }
        else{
            setrt(0)
        }

    }
    useEffect(()=>{
        dispatch(getallorders())
        dispatch(getselectedorders())
        dispatch(setroute())
        dispatch(getroutes())

    },[dispatch])
    //console.log(selectedorders)

    function  handleselect(id){
        dispatch(selection(id))
        sets(1)
     }
     function handlecreateroute(){
        sets(1)
     }

    return (
    <div className='container-fluid'>
        <div className='row '>
            
                <div className="col-md-3 col-xs-12  col-lg-2 sidebar-offcanvas pl-0 side sticky-top " id="sidebar" role="navigation" style={{backgroundColor:"#e9ecef"}}>
                    <ul className="nav flex-column sticky-top pl-0 pt-5 p-3 mt-3">
                        <center>
                        <li className="nav-item mb-2 mt-3"><a className="nav-link text-secondary" href="#"><h5><b>{u.name.toUpperCase()}</b></h5></a></li>
                        <li><img src={student1} className="imagestyle"/></li>
                        <li className="nav-item mb-2 mt-3"><a class="nav-link text-secondary" href="#"><h5><b></b></h5></a></li>
                        <li className={s===0?"nav-item mb-2 mt-3 bghighlight":"nav-item mb-2 mt-3"} onClick={handleallorders}><a className="nav-link text-secondary" ><h5>All Orders</h5></a></li>
                        <li className={s===1?"nav-item mb-2 mt-3 bghighlight":"nav-item mb-2 mt-3"} onClick={handleselectedorders}><a className="nav-link text-secondary"><h5>Selected Orders</h5></a></li>
                        
                        </center>
                        
                    </ul>
            </div>
            <div className='col-md-9 col-sm-12 pt-5 mt-3'>
                    <div className={(s===0 && s!==1 )?'dis':'hide'}>
                        <table className='table table-striped table-light table-hover table-responsive table-sortable mt-5 container border py-2 w-100'>
                                        <thead>
                                            <tr>
                                                <th>S.No</th>
                                                <th>Customer Name</th>
                                                <th>Phone Number</th>
                                                <th>Address</th>
                                                <th>Order ID</th>
                                                <th onClick={()=>{pd===1?setpd(0):setpd(1)}} >Products</th>
                                                <th>Amount</th>
                                                <th>Order Date</th>
                                                <th>Select</th>
                                            
                                            </tr>
                                        </thead>      
                            <tbody>
                            {allorders&&allorders.map((order, index) => {
                                return(
                                    <tr>
                                    <td>{index+1}</td>
                                    <td>{order.customer}</td>
                                    <td>{order.phonenumber}</td>
                                    <td>{order.address.address}</td>
                                    <td>{order.id}</td>
                                    <td onClick={()=>{pd===1?setpd(0):setpd(1)}}>{order.products.length}
                                    <table className={pd===1?'dis':'hide'}>
                                        <thead>
                                            <tr>
                                                <td>Name</td>
                                                <td>Price</td>
                                                <td>Quantity</td>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {
                                                order.products.map((ep)=>{
                                                return(
                                                    <tr>
                                                    <td>{ep.name}</td>
                                                    <td>{ep.price}</td>
                                                    <td>{ep.quantity}</td>
                                                    </tr>
                                                    )
                                                })
                                                }
                                        </tbody>
                                    </table>
                                    
                                    
                                    </td>
                                    <td>Rs.{order.amount}</td>
                                    <td>{order.ordered_at}</td>
                                    <td><input type="checkbox" className='form-control' onClick={()=>{handleselect(order.id)}}/></td>

                                    </tr>
                                    )
                                
                            })}
                                </tbody>
                        </table>
                            
                    </div>
                    <div className={(s===1 && s!==0 )?'dis':'hide'}>
                        <table className='table table-striped table-light table-hover table-responsive table-sortable mt-5 container border py-2 w-100'>
                                        <thead>
                                            <tr>
                                                <th>S.No</th>
                                                <th>Customer Name</th>
                                                <th>Phone Number</th>
                                                <th>Address</th>
                                                <th>Order ID</th>
                                                <th onClick={()=>{pd===1?setpd(0):setpd(1)}} >Products</th>
                                                <th>Amount</th>
                                                <th>Order Date</th>
                                                
                                            
                                            </tr>
                                        </thead>      
                            <tbody>
                            {selectedorders&&selectedorders.map((order, index) => {
                                return(
                                    <tr>
                                    <td>{index+1}</td>
                                    <td>{order.customer}</td>
                                    <td>{order.phonenumber}</td>
                                    <td>{order.address.address}</td>
                                    <td>{order.id}</td>
                                    <td onClick={()=>{pd===1?setpd(0):setpd(1)}}>{order.products.length}
                                    <table className={pd===1?'dis':'hide'}>
                                        <thead>
                                            <tr>
                                                <td>Name</td>
                                                <td>Price</td>
                                                <td>Quantity</td>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {
                                                order.products.map((ep)=>{
                                                return(
                                                    <tr>
                                                    <td>{ep.name}</td>
                                                    <td>{ep.price}</td>
                                                    <td>{ep.quantity}</td>
                                                    </tr>
                                                    )
                                                })
                                                }
                                        </tbody>
                                    </table>
                                    
                                    
                                    </td>
                                    <td>Rs.{order.amount}</td>
                                    <td>{order.ordered_at}</td>
                                    

                                    </tr>
                                    )
                                
                            })}
                                </tbody>
                        </table>
                        <button  onClick={()=>{handleroutedis()}}>CreateRoute</button>
                        <div className={rt===1?'dis':'hide'}>
                            <h1>hello</h1>
                                {routes&&routes.map((route)=>{
                                    return(
                                        <span>{route.address}</span>
                                    )
                                })}
                        </div>
                            
                    </div>
                        

                </div>
        </div>
    </div>
       
    )
}
 
export default Dashboard